<template>
  <div>
    <v-app-bar
      color="blue darken-2"
      dense
      dark
      app
    >
      <v-app-bar-nav-icon @click="showSidebar()"></v-app-bar-nav-icon>

      <v-toolbar-title @click="redirect({ name: 'Home' })" style="cursor: pointer">{{ app.name }}</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn icon @click="newTab(`tel:${$store.state.social.phone}`, false)">
        <v-icon>mdi-phone</v-icon>
      </v-btn>

      <v-btn icon @click="newTab(`mailto:${$store.state.social.email}`, false)">
        <v-icon>mdi-email</v-icon>
      </v-btn>

      <v-btn icon @click="newTab($store.state.social.linkedin)">
        <v-icon>mdi-linkedin</v-icon>
      </v-btn>

      <v-btn icon @click="newTab($store.state.social.facebook)">
        <v-icon>mdi-facebook</v-icon>
      </v-btn>

    </v-app-bar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      app: this.$store.state.app,
    }
  },
  methods: {
    showSidebar() {
      this.$store.state.showSidebar = true;
    }
  },
}
</script>