<template>
  <v-navigation-drawer
    v-model="$store.state.showSidebar"
    absolute
    temporary
    :style="style"
  >
    <v-list-item>
      <v-list-item-avatar>
        <v-img :src="publicPath + 'assets/img/deyvid-holz.jpg'"></v-img>
      </v-list-item-avatar>

      <v-list-item-content>
        <v-list-item-title>{{ app.author }}</v-list-item-title>
      </v-list-item-content>
    </v-list-item>

    <v-divider></v-divider>

    <v-list dense>
        <v-list-item 
          v-for="item in items"
          :key="item.title"
          link
          @click="redirect(item.href)"
        >
          <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
  export default {
    data () {
      return {
        app: this.$store.state.app,
        publicPath: process.env.BASE_URL,
        items: [
          { title: 'Home', icon: 'mdi-home', href: 'Home' },
          { title: 'About me', icon: 'mdi-information', href: 'About' },
          { title: 'My projects', icon: 'mdi-folder-open', href: 'Projects' },
        ],

        style: { position: 'fixed' }
      }
    },
    methods: {
      redirect(routerName) {
        this.$router.push({ name: routerName })
      }
    }
  }
</script>