<template>
  <v-card
    class="mx-auto"
    max-width="344"
    outlined
  >
    <v-list-item three-line>
      <v-list-item-content>
        <div class="overline mb-4">
          <div class="progress-bar">
            <div class="progress" :style="'width:' + skill.progress + '%; height: 100%;'" v-if="skill.progress === 100"></div>
            <div class="progress noradius" :style="'width:' + skill.progress + '%; height:100%;'" v-else></div>
          </div>
        </div>
        <v-list-item-title class="headline mb-1">{{ skill.name }}</v-list-item-title>
      </v-list-item-content>

      <v-list-item-avatar
        tile
        size="80"
      >
        <v-img class="skill-image" :src="publicPath + 'assets/img/skills/' + skill.image"></v-img>
      </v-list-item-avatar>
    </v-list-item>
  </v-card>
</template>

<script>
export default {
  props: ['skill'],
  data() {
    return {
      publicPath: process.env.BASE_URL,
    }
  }
}
</script>

<style scoped>
.progress-bar {
  width: 100%;
  border: 1px solid #BDBDBD;
  height: 11px;
  border-radius: 20px;
}

.progress-bar .progress {
  background-color: #1E88E5;
  width: 100%;
  height: 11px;
  border-radius: 20px;
}

.noradius {
  border-top-right-radius: 0 !important;
  border-bottom-right-radius: 0 !important;
}

.skill-image {
  filter: grayscale(1);
  transition: .53s;
}

.skill-image:hover {
  filter: grayscale(0);
}
</style>